.donate {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.button {
  background-color: transparent;
  border: dotted 1px red;
  border-radius: 10px;
  color: red;
  padding: 5px 10px;
}

.button2 {
  display: inline-block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background-size: contain;
  background-image: url('./heart.png');
  color: #FFF;
  font-size: 20px;
  opacity: 0.8;
}

.button2:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.button2-animate {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-name: animate;
          animation-name: animate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: cubic-bezier(.65,0,.26,1.01);
          animation-timing-function: cubic-bezier(.65,0,.26,1.01);
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes animate {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  50% {
   -webkit-transform: scale(1.3);
           transform: scale(1.3);
  }

  to {
   -webkit-transform: scale(1);
           transform: scale(1);
 }
}

@keyframes animate {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  50% {
   -webkit-transform: scale(1.3);
           transform: scale(1.3);
  }

  to {
   -webkit-transform: scale(1);
           transform: scale(1);
 }
}

.donate-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.qrcodes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 500px;
  -ms-flex-align: center;
      align-items: center;
}

.image {
  width: 200px;
  height: 200px;
  margin-bottom: 30px;
}

.desc {
  text-align: center;
}



